
<template>
  <div id="userCenter">
      <Nav page="userCenter"/>

      <div class="space30"></div>

      <div class="mainContent flex start">
        <div class="menu shadownItemBox">

          <div class="userInfo">
            <img :src="userInfo.user.userHead ? setImgUrl(userInfo.user.userHead) : headImg " class="userImg" alt="">

            <div class="userName"><b>{{userInfo.user.userName}}</b>
              <small v-if="userInfo.user.area">+{{userInfo.user.area}} </small>
              <small>{{userInfo.user.registerAccount}}</small>
            </div>

            <div class="textCenter cursor" @click="gopage('/userCenter/safe')">
              <span class="tag" v-if="statue=='success'">已认证</span>
              <span class="tag type2" v-else-if="statue=='failed'">认证失败</span>
              <span class="tag type3" v-else-if="statue=='progress'">认证中</span>
              <span class="tag type4" v-else>未认证</span>
            </div>
          </div>
          <!-- userInfo -->

          <div class="menuGroup">
            <div class="menuItem flex align" 
              v-for="(item,index) in menuList" 
              :class="{active : $route.path.indexOf(item.path)>0}"
              :key="index" 
              @click="gopage(`/userCenter/${item.path}`)"><i :class="`${item.icon} ${$route.path.indexOf(item.path)>0 ? 'animated bounceIn':''}`"></i><span>{{item.title}}</span></div>
              <!-- <i :class="`menuIocn icon${++index}`"></i> -->
          </div>
          <!-- menuGroup -->


        </div>
        <!-- menu -->

        <div class="router shadownItemBox">
          <router-view></router-view>
        </div>
        <!-- router -->

      </div>

      <div class="space20"></div>

      <Footer/>
  </div>
  <!-- index -->
</template>

<script>
  import {
    mapMutations,
    mapState,
    mapActions
  } from 'vuex'

  import Nav from './nav'
  import Footer from './footer'

  import headImg from '../assets/img/userIocn.png'

  
  import {
    certificationStatue 
  } from '../data/apis';
  
  
  export default {
    data() {
      return {
        headImg,
        statue:null,//认证状态

        menuList: [{
            path: 'assets',
            icon:'el-icon-data-analysis',
            title: "用户资产"
          },
          {
            path: 'inOut',
            icon:'el-icon-money',
            title: "用户充提"
          },
          {
            path: 'history',
            icon:'el-icon-notebook-1',
            title: "认购记录"
          },
          {
            path: 'share',
            icon:'el-icon-position',
            title: "推荐分享"
          },
          {
            path: 'safe',
            icon:'el-icon-unlock',
            title: "安全设置"
          },
          {
            path: 'setting',
            icon:'el-icon-cpu',
            title: "账号设置"
          },
        ]
      }
    },
    components: {
      Nav,Footer
    },
    computed: {
      ...mapState(['userInfo'])
    },
    methods: {
      ...mapMutations(['setUserInfo']),
      ...mapActions(['actionUserInfo']),
      checkStatue(){//，检查认证状态
        certificationStatue().then(v=>{
          console.log(v)
          this.statue = v.data.state;
        })
      }//checkCertificationStatue
    },
    mounted() {
      this.actionUserInfo();
      this.checkStatue();
    } //mounted
  };

</script>
<style rel="stylesheet/scss" scoped lang="scss">
  @import "../styles/Val";

  .menu{
    width: 260px;
    margin-right: 30px;
    .userInfo{
      text-align: center;
      margin-bottom: 30px;
      .userImg{
        width: 100px;
        height: 100px;
        display: block;
        margin: 10px auto;
        border-radius: 100px;
        background: rgba(0,0,0,0.05);
      }
      .userName{
        b{
          font-size: 16px;
          display: block;
          margin-bottom: 3px;
        }
        small{
          color: $them_color_gray_light;
          font-size: 12px;
        }
      }
      .tag{
        background:$them_color url('../assets/img/tag.png') no-repeat;
        background-size:15px;
        background-position:10px;
        color: white;
        border-radius: 100px;
        padding: 5px 10px;
        padding-left: 30px;
        font-size: 12px;
        margin-top: 10px;
        display: inline-block;

        &.type2{
          background-color:#f56c6c !important;
        }
        &.type3{
          background-color:#24c132 !important;
        }
        &.type4{
          background-color:#f56c6c !important;
        }
      }
    }
    .menuGroup{
      .menuItem{
        i{
          margin-right: 20px;
          width: 35px;
          height: 35px;
          text-align: center;
          line-height: 35px;
          border-radius: 100px;
          font-size: 25px;
          // background: rgba(0,0,0,0.1);
          // border: 1px solid $them_color_gray_light;
          color: $them_color_gray_light;
        }
        span{
          font-size: 16px;
          color: $them_color_gray;
        }
        &.active{
          i{
            font-size: 20px;
          }
          span{
            color: $them_color;
            font-weight: 500;
          }
          i{
            background: $them_color;
            border: 1px solid $them_color;
            color: white;
            font-weight: normal;
          }
          
        }
        .menuIocn{
          width: 20px;
          height: 20px;
          margin-right: 20px;
          background-size:100% 100% !important;
          /*&.icon1{
            background: url('../assets/img/menu1.png');
          }
          &.icon2{
            background: url('../assets/img/menu2.png');
          }
          &.icon3{
            background: url('../assets/img/menu3.png');
          }
          &.icon4{
            background: url('../assets/img/menu4.png');
          }
          &.icon5{
            background: url('../assets/img/menu5.png');
          }
          &.icon6{
            background: url('../assets/img/menu6.png');
          }*/
        }
        cursor: pointer;
        padding: 15px 0;
        margin-bottom: 15px;
        font-size: 16px;
      }
    }
  }
  
</style>






